<script lang="ts" setup>
import {ref} from 'vue'
import {message} from 'ant-design-vue'
import type {VxeGridListeners} from 'vxe-table'
import {destroy} from "@/api/identityController";
import useIdentity from "@/composible/useIdentity";

const serveApiUrl = import.meta.env.VITE_API_URL;

const {gridOptions} = useIdentity()
const xGrid = ref()

// 编辑用户
const editCourse = (row: any) => {
  const grid = xGrid.value
  grid.setEditRow(row)
}

// 删除用户
const delCourse = async (row: any) => {
  try {
    await destroy({id: row.id})
    xGrid.value.commitProxy('query')
  } catch (error) {
    message.error('删除失败')
  }
}


const gridEvent: VxeGridListeners = {
  proxyQuery() {
    /*设置选项*/
    const column = xGrid.value.getColumnByField('venue_id')

  },
}
</script>

<template>
  <div class="demo-page-wrapper">
    <vxe-grid
        ref="xGrid"
        v-bind="gridOptions"
        v-on="gridEvent"
    >
      <template #head_shot="{ row }">
        <div style="display:flex;flex-direction: column;">
          <vxe-image :src="`${serveApiUrl}${row.head_shot}`" width="64" height="64"></vxe-image>
          <FileSelect ref="fileRef" v-model="row.head_shot">
          </FileSelect>
        </div>
      </template>

      <template #id_card_front="{ row }">
        <div style="display:flex;flex-direction: column;">

          <vxe-image :src="`${serveApiUrl}${row.id_card_front}`" width="64" height="64"></vxe-image>
          <FileSelect ref="fileRef" v-model="row.id_card_front">
          </FileSelect>
        </div>
      </template>

      <template #certificate="{ row }">
        <div style="display:flex;flex-direction: column;">

          <vxe-image :src="`${serveApiUrl}${row.certificate}`" width="64" height="64"></vxe-image>
          <FileSelect ref="fileRef" v-model="row.certificate">
          </FileSelect>
        </div>
      </template>
      <template #exam_report="{ row }">
        <div style="display:flex;flex-direction: column;">

          <vxe-image :src="`${serveApiUrl}${row.exam_report}`" width="64" height="64"></vxe-image>
          <FileSelect ref="fileRef" v-model="row.exam_report">
          </FileSelect>
        </div>
      </template>
      <template #resume="{ row }">
        <div style="display:flex;flex-direction: column;">

          <vxe-image :src="`${serveApiUrl}${row.resume}`" width="64" height="64"></vxe-image>
          <FileSelect ref="fileRef" v-model="row.resume">
          </FileSelect>
        </div>
      </template>
      <template #action="{ row }">
        <a-space>
          <a-popconfirm
              title="确认删除吗？"
              ok-text="是"
              cancel-text="否"
              @confirm="delCourse(row)"
          >
            <a-button size="small" type="primary" danger>删除</a-button>
          </a-popconfirm>
        </a-space>
      </template>
    </vxe-grid>
  </div>
</template>

<style scoped lang="less">
.demo-page-wrapper {
  padding: 20px;
}
</style>